
import React, { useReducer } from 'react';
import { Grid } from "@material-ui/core";
import UserContext from '../store/context/User';
import { useLocation } from 'react-router-dom';
import RolesContext from '../store/context/Role';
import RolesReducer from '../store/reducers/Roles';
import PageContainer from './Role/PageContainer';
import PageAddEditContainer from './Role/PageAddEditContainer';


const RolePage = props => {
  let pageType = useLocation().pathname;
  let editId = pageType.split('/')[5] ? pageType.split('/')[5] : null;
  console.log(editId);
  const initialRolesState = {
    roles: [
        {
          "id": 1,
          "name": "applicant",
          "title": "Applicant",
          "description": ""
        },{
          "id": 2,
          "name": "manager",
          "title": "Manager",
          "description": ""
        },{
            "id": 3,
            "name": "inspector",
            "title": "Inspector",
            "description": ""
        },{
          "id": 4,
          "name": "recipient",
          "title": "Recipient",
          "description": ""
      }
    ]
  };
  const [state, dispatch] = useReducer(RolesReducer, initialRolesState);

  return (
      <RolesContext.Provider value={{state, dispatch}}>
        {pageType.includes('add') || pageType.includes('edit') ? 
            (
                <PageAddEditContainer 
                    pageType={pageType}
                    editId={editId}
                />
            ) : (
                <PageContainer />
            )
        }
      </RolesContext.Provider>
  );
};

export default RolePage;

