import React, { useContext } from "react";
import { Card, CardContent, Paper } from './Styles';
import { Grid, Button, TextField } from "@material-ui/core";
import RolesContext from '../../store/context/Role';

const Forms = props => {
  const { pageType, editId } = props;
  const project = useContext(RolesContext);
  const projectState = project.state;
  const editItem = projectState.projects.filter(item => item.id.toString() === editId);

  console.log(editItem);

  return (
    <Card>
      <CardContent>
        <Paper>
        <form noValidate autoComplete="off">
          <Grid container item lg={12}>
            <Grid container item lg={6} md={4}>
              <TextField
                id="name"
                label="Name"
                style={{ margin: 8 }}
                placeholder="Name"
                defaultValue={pageType.includes('edit') ? editItem[0]['name'] : ''}
                fullWidth
                m={5}
              />
            </Grid>
            <Grid container item lg={6} md={8}>
              <TextField
                id="description"
                label="Description"
                style={{ margin: 8 }}
                placeholder="Description"
                defaultValue={pageType.includes('edit') ? editItem[0]['description'] : ''}
                fullWidth
                m={5}
              />
            </Grid>
            <Grid container item lg={12} >
              <TextField
                id="extra"
                label="Extra"
                style={{ margin: 8 }}
                defaultValue={pageType.includes('edit') ? JSON.stringify(editItem[0]['extra']) : ''}
                placeholder="Extra"
                multiline
                rows="5"
                fullWidth
                m={2}
              />  
            </Grid>
          </Grid>
          <Grid container item lg={12} justify="flex-end">
            <Button
              variant="contained"
              size="medium"
              style={{ margin: '25px 8px 0 8px' }}
            >
              Reset
            </Button>

            <Button
              variant="contained"
              color="primary"
              size="medium"
              style={{ margin: '25px 8px 0 8px' }}
              target="_self"
            >
              Save
            </Button>
          </Grid>
        </form>
        </Paper>
      </CardContent>
    </Card>
  );
}

export default Forms;
