import React from 'react';
import { NavLink as RouterNavLink } from "react-router-dom";
import Helmet from 'react-helmet';
import { Grid, Link } from '@material-ui/core';
import { Card, Typography, Paper, Button, Divider, Breadcrumbs, AddButtonContainer } from './Styles';
import Form from './Form';

const NavLink = React.forwardRef((props, ref) => (
  <RouterNavLink innerRef={ref} {...props} />
));

const PageAddEditContainer = props => {
  const {pageType, editId } = props;
  return ( 
    <React.Fragment>
      <Helmet title="Projects" />
      <Typography variant="h3" gutterBottom>
          Projects
      </Typography>
      <Breadcrumbs aria-label="Breadcrumb" mt={2}>
          <Link component={NavLink} exact to="/web/admin/projects">
            Admin
          </Link>
          <Link component={NavLink} exact to="/web/admin/projects">
            Projects
          </Link>
          {pageType.includes('add') ? <Typography>Add New Project</Typography> :
            pageType.includes('edit') ? <Typography>Edit Project</Typography> : null
          }
      </Breadcrumbs>
      <Divider my={6} />
      {pageType.includes('add') ? 
        <AddButtonContainer container item lg={12} md={12} spacing={2}>
          <h3>Add New Project</h3>
        </AddButtonContainer>
        : null
      }
      <Grid container item lg={12} md={12}>
        <Form pageType={pageType} editId={editId} />
      </Grid>
    </React.Fragment>
  );
}
 
export default PageAddEditContainer;